import App from '../App'
import Router from 'vue-router'

Router.prototype.go = function(t) {
  this.isBack = true
  console.log('go===' + this.history.current.name)
  let currentName = this.history.current.name
  if (!t) window.history.go(-1)
  else window.history.go(t)
  var self = this
  setTimeout(function() {
    if (currentName === self.history.current.name) {
      try {
        uexWindow.close(-1)
      } catch (e) {}
    }
  }, 100)
}
Router.prototype.back = function(t) {
  this.isBack = true
  let currentName = this.history.current.name
  if (t) window.history.back()
  else window.history.back()
  var self = this
  setTimeout(function() {
    if (currentName === self.history.current.name) {
      try {
        uexWindow.close(-1)
      } catch (e) {}
    }
  }, 100)
}

const index = r => require.ensure([], () => r(require('../pages/index/index')), 'index')
const creatQuestionList = r => require.ensure([], () => r(require('../pages/index/creatQuestionList')), 'creatQuestionList')
const getQustionList = r => require.ensure([], () => r(require('../pages/index/getQustionList')), 'getQustionList')
const search = r => require.ensure([], () => r(require('../pages/search')), 'search')
const toDo = r => require.ensure([], () => r(require('../pages/toDo')), 'toDo')
const finishDo = r => require.ensure([], () => r(require('../pages/finishDo')), 'finishDo')
const preview = r => require.ensure([], () => r(require('../pages/preview')), 'preview')
const creatQuestion = r => require.ensure([], () => r(require('../pages/creatQuestion')), 'creatQuestion')
const selectQuestion = r => require.ensure([], () => r(require('../pages/selectQuestion')), 'radio')
const investigate = r => require.ensure([], () => r(require('../pages/investigate/investigate')), 'investigate')

const routes = [
  {
    path: '',
    component: App,
    children: [
      {
        name: 'index',
        path: 'index',
        component: index,
        redirect: '/index/getQustionList',
        children: [
          {
            name: 'creatQuestionList',
            path: 'creatQuestionList',
            component: creatQuestionList
          },
          {
            name: 'getQustionList',
            path: 'getQustionList',
            component: getQustionList
          }
        ]
      },
      {
        path: '/search',
        name: 'search',
        component: search,
        meta: {
          keepAlive: true
        }
      },
      {
        path: '/toDo/:id/:quesWj',
        name: 'toDo',
        component: toDo,
        meta: {
          keepAlive: false
        }
      },
      {
        path: '/finishDo/:id/:random',
        name: 'finishDo',
        component: finishDo,
        meta: {
          keepAlive: false
        }
      },
      {
        path: '/preview',
        name: 'preview',
        component: preview,
        meta: {
          keepAlive: false
        }
      },
      {
        path: '/creatQuestion',
        name: 'creatQuestion',
        component: creatQuestion,
        meta: {
          keepAlive: false
        }
      },
      {
        path: '/selectQuestion',
        name: 'selectQuestion',
        component: selectQuestion,
        meta: {
          keepAlive: false
        }
      },
      {
        path: '/investigate/:id/:quesWj',
        name: 'investigate',
        component: investigate,
        meta: {
          keepAlive: false
        }
      }
    ]
  }
]

export default routes



