export default {
  namespaced: true,
  state: {
    answer: {}
  },
  getters: {
    answer: state => {
      console.log('state',this.state.answer)
      return this.state.answer
    }
  },
  mutations: {
    newAnswer (state, val) {
      console.log('state1',state)
      console.log('val',val)
      if (val == "del") {
        this.state.answer = {}
      } else {
        for (var p in val) {
          this.state.answer[p] = val[p]
        }
      }
      console.log("55555555555555",state.answer)
    }
  }
}



