import Vue from 'vue'
import Vuex from 'vuex'
import submitModule from './submit/index'

Vue.use(Vuex)
//定义state，并将listName设置为一个空对象

const state = {
  isLogin: '', // 是否登录，空为未登录
  device: 'andriod' // 设备
}
//定义mutations，可以传参，用于设置state里的listName
const mutations = {
  set_isLogin: (state, value) => {
    state.isLogin = value
  },
  setDevice: (state, value) => {
    state.device = value
  }
}
//定义getters，用于获取出state里的对象
const getters = {
  isLogin: state => {
    return state.isLogin
  },
  device: state => {
    return state.device
  }
}

export default new Vuex.Store({
  getters,
  state,
  mutations,
  modules: {
    submit: submitModule
  }
})



