import { mapGetters, mapMutations } from "vuex";
import Api from "@/js/common";
import { setTimeout } from "timers";

export default {
  name: "app",
  computed: {
    ...mapGetters(["isLogin"])
  },
  mounted() {
    setTimeout(() => {
      appcan.showMicroAppBackButton({backButton:true});
      // appcan.closeCurrentMicroApp();
      this.checkOS();
      if (!this.isLogin) {
        this.login();
      }
    },1000);
    
  },
  methods: {
    ...mapMutations(["setDevice", "set_isLogin"]),
    login() {
      var vmSelf = this;
      var submitObj = {
        // token: JSON.parse(Api.getVal('emmToken')).info.accessToken
        token: "d946ec54-c3bd-465e-b3b5-015f77d31997"
      };
      vmSelf.$vux.loading.show();
      Api.login(submitObj)
        .then(function(response) {
          vmSelf.$vux.loading.hide();
          if (response.status == "0") {
            vmSelf.set_isLogin("true");
            Api.setVal("sid", response.sid);
            vmSelf.$router.push({ name: "index" });
          } else {
            vmSelf.set_isLogin("");
            Api.setVal("sid", "");
            Api.errFn(1);
            vmSelf.$router.push({ name: "index" });
          }
        })
        .catch(function(err) {
          console.log("=====", err);
        });
    },
    checkOS() {
      var ua = navigator.userAgent.toLowerCase();
      if (/iphone|ipad|ipod/.test(ua)) {
        this.setDevice("ios");
      }
    },
    alertInfo(title) {
      var vmSelf = this;
      this.$vux.alert.show({
        title: "提示",
        content: title,
        onShow() {
          console.log("Plugin: I'm showing");
        },
        onHide() {
          console.log("Plugin: I'm hiding now");
        }
      });
    }
  }
};



